.App{
  width: 40%;
  margin-left: auto;
  margin-right: auto;
  height: 50vh;
  background-color: black;
  color: white;
  margin-top: 20vh;
}

.title{
  text-align: center;
  margin-top: 5%;
}


.phone_form{
  margin-top: 10vh;
  margin-left: auto;
  margin-right: auto;
  width: 40%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.phone_num, input {
  background-color: black;
}

.num_block
{
  height: 30px;
  color: white;
  font-size: 20px;
  padding: 2%;
  border-radius: 5px;
  border: 1px solid white;
}
.code{
  width: 40px;
  text-align: center;
  margin-right: 5%;
  
}
.p_number{
  padding-left: 5%;
}

.btn{
  width: fit-content;
  margin: auto;
  margin-top: 10vh;
}

.submit-button{
  height: 50px;
  font-size: 25px;
  width: max-content;
  margin: auto;
  color: white;
  background-color: black;
  border: 1px solid white;
  padding-left: 5%;
  padding-right: 5%;
  border-radius: 5px;
}

.otp-part{
  margin-top: 15vh;
  text-align: center;
}

.otp-box{
  width: 40px;
  height: 40px;
  margin-right: 5%;
  color: white;
  text-align: center;
}

.otp-field{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

@media only screen and (max-width: 600px) {
  .App{
    width: 100%;
    
  }

}